@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
  color: var(--text-color__black);
}

*::selection {
  background-color: var(--bg-color-secondary);
  color: var(--text-color__black);
}

:root {
  --bg__color: #19141f;
  --color_secondary3: #31283e;
  --color_secondary2: #9c6392;
  --text-color-primary: #f2eff5;
  --text-color-secondary: #b0a2c3;
  --black__color: #1b1b1b;
  --bg-color-secondary: #4a3c5d;

  --text-color__black: #0f0f0f;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--black__color);
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background: var(--bg__color);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--bg-color-secondary);
}

a {
  color: var(--text-color-primary);
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  border: none;
  cursor: pointer;
  background: none;
}

progress {
  display: block;
  -webkit-appearance: none;
}

input {
  background-color: var(--text-color-primary);
  border: none;
  outline: none;
  font-size: 1.2em;
  padding: 5px;
}

.rating__bar {
  padding: 1em 0px;
}

.rating__bar::-webkit-progress-value {
  background: -webkit-linear-gradient(
    left,
    var(--bg-color-secondary) 40%,
    var(--color_secondary2) 100%
  );
  border-radius: 5px;
}

.rating__bar::-webkit-progress-bar {
  background: var(--text-color-primary);
  border-radius: 5px;
  height: 10px;
}

.btn {
  padding: 0.7em;
  background-color: var(--bg-color-secondary);
  border-radius: 5px;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.2s;
}
